
import $ajax from '@/untils/ajax-service.js'
import API_LIST from '@/untils/config.js'

/**
 * @description 一键登录
 */
const LOGIN_ACTIONS = {
  async login ({ commit, state }) {
    const REQ_PARAMS = {
      url: API_LIST.login,
      param: {
        type: 'login'
      }
    }
    return new Promise((resolve, reject) => {
      $ajax.get(REQ_PARAMS).then(res =>{
        if(res.status == 200) {
          console.log(res.headers)
          localStorage.setItem("token",res.headers.authorization);
          commit('setUserInfo', res.headers.authorization)
        }
        resolve(res)
      })
    })
  },
  //导入基本信息
  async reLoginLocal ({ commit, state }) {
    commit('setBaseInfo')   
  }
}

export default LOGIN_ACTIONS



