
import CITY_ACTIONS from './actions/city'

const CITY = {
  namespaced: true,
  state: {
    cityList: []
  },
  actions: {
    ...CITY_ACTIONS
  },
  mutations: {
    setCityList (state, data) {
      state.cityList = data
    }
  }
}
export default CITY



