
import axios from 'axios'
import qs from 'qs'
import Vue from 'vue'
import router from '../router/index'
axios.defaults.timeout = 15000 // 请求超时时间

axios.defaults.timeout = 15000 // 请求超时时间
const showToast = (tip = '数据加载中') => {
  Vue.$vux.loading.show()
}

axios.interceptors.response.use(function (request) {
  console.log(request)
  // Do something with response data
  if (request.data && request.headers['Content-Type'] === 'application/x-www-form-urlencoded') {
    request.data = qs.stringify(request.data);
}
return request;
}, function (error) {
  console.log(error.response)
  // alert(error.response.data.message)
  Vue.$vux.alert.show({
    title: '提示',
    content: error.response.data.message
  })
  // Do something with response error
  return Promise.reject(error);
});


function errorToast (e) {
  // console.log(e)
  // let info
  // if (e.code) {
  //   info = '数据请求失败'
  // } else {
  //   info = '请检查网络'
  // }
  // alert(info)
}
const $ajax = {
  async get (obj, tag = true) {
    if (tag) {
      showToast()
    }
    try {
      console.log(obj)
      // console.log(obj.param.type == 'login')
      // if(obj.param.type == 'login'){
      //   console.log('登录')
      // }
      // else if(obj.param.type == 'undefined') {
      //   if(obj.header.Authorization == null) {
      //     router.push({ name: 'login' })
      //     return
      //   }
      // }
      let res = await axios.get(obj.url, { params: obj.params || '', headers: obj.header || '' })
      Vue.$vux.loading.hide()
      res = res
      return new Promise((resolve, reject) => {
        if (res.code === 0 || res.stauts === true) {
          resolve(res)
        } else {
          resolve(res)
        }
      })
    } catch (err) {
      errorToast(err)
      Vue.$vux.loading.hide()
    }
  },
  async post (obj, tag = true) {
    if (tag) {
      showToast()
    }
    try {
      console.log(obj.params)
      let res = await axios.post(obj.url, qs.parse(obj.params), {headers: obj.header});
      Vue.$vux.loading.hide()
      res = res.data
      return new Promise((resolve, reject) => {
        if (res.code === 0 || res.status === true) {
          resolve(res)
        } else {
          resolve(res)
        }
      })
    } catch (err) {
      errorToast(err)
      Vue.$vux.loading.hide()
    }
  }
}

export default $ajax



