
import LOGIN_ACTIONS from './actions/login'

const LOGIN = {
  namespaced: true,
  state: {
    userInfo: {
      authorization:''
    }
  },
  getters: {
    accountToken (state) {
      return state.userInfo.authorization
    }
  },
  actions: {
    ...LOGIN_ACTIONS
  },
  mutations: {
    setUserInfo (state, data) {
      state.userInfo.authorization = data
    },
    // 基础信息
    setBaseInfo (state) {
      state.userInfo.authorization = localStorage.getItem("token");
    }
  }
}
export default LOGIN



