
import PASSENGER_ACTIONS from './actions/passenger'

const PASSENGER = {
  namespaced: true,
  state: {
    passengerLis: []
  },
  actions: {
    ...PASSENGER_ACTIONS
  },
  mutations: {
    setPassengerList (state, data) {
      state.passengerLis = data
    }
  }
}
export default PASSENGER



