
import $ajax from '@/untils/ajax-service.js'
import API_LIST from '@/untils/config.js'
import store from '@/store/index'

/**
 * @description 新增订单
 */
const ORDER_ACTIONS = {
  async saveOrder ({ commit, state }, data) {
    const REQ_PARAMS = {
      url: API_LIST.order_save,
      params: data,
      header:{
        'Authorization': store.getters['LOGIN/accountToken']
      }
    }
    return new Promise((resolve, reject) => {
      $ajax.post(REQ_PARAMS).then(res =>{
        if(res.status == 200) {
        //   commit('setCityList', res.data)
        }
        resolve(res)
      })
    })
  }
}

export default ORDER_ACTIONS



