
import $ajax from '@/untils/ajax-service.js'
import API_LIST from '@/untils/config.js'

/**
 * @description 获取火车票列表
 */
const TICKET_ACTIONS = {
  async getTicketList ({ commit, state }, data) {
    const REQ_PARAMS = {
      url: API_LIST.ticket_list,
      params: data
    }
    return new Promise((resolve, reject) => {
      $ajax.get(REQ_PARAMS).then(res =>{
        if(res.status == 200) {
          console.log(res)
          commit('setTicketList', res.data)
        }
        resolve(res)
      })
    })
  },
  // async getTicketDetail ({ commit, state }, id) {
  //   const REQ_PARAMS = {
  //     url: API_LIST.ticket_detail,
  //     params: {
  //       id
  //     }
  //   }
  //   const DATA = await $ajax.get(REQ_PARAMS)
  //   commit('setTicketDetail', DATA.data)
  // }
}

export default TICKET_ACTIONS



