import Vue from 'vue'
import Router from 'vue-router'
// import index from '../views/index.vue'

/**
 * 城市列表
 */
const login = (resolve) => { require(['../views/login.vue'], resolve) }

/**
 * 城市列表
 */
const cityList = (resolve) => { require(['../views/city/list.vue'], resolve) }

/**
 * 车票预订
 */
const index = (resolve) => { require(['../views/ticket/index.vue'], resolve) }
const ticketList = (resolve) => { require(['../views/ticket/ticketList.vue'], resolve) }


/**
 * 乘客信息
 */
const riderList = (resolve) => { require(['../views/person/riderList.vue'], resolve) }
const addRider = (resolve) => { require(['../views/person/addRider.vue'], resolve) }

Vue.use(Router)
Router.prototype.goBack = function () {
  this.isBack = true
  this.back()
}

export default new Router({
  routes: [
    {
      path: '/',
      name: 'index',
      component: index
    },
    {
      path: '/login',
      name: 'login',
      component: login
    },
    {
      path: '/ticketList',
      name: 'ticketList',
      component: ticketList
    },
    {
      path: '/cityList',
      name: 'cityList',
      component: cityList
    },
    {
      path: '/riderList',
      name: 'riderList',
      component: riderList
    },
    {
      path: '/addRider',
      name: 'addRider',
      component: addRider
    },
  ]
})



