
import $ajax from '@/untils/ajax-service.js'
import API_LIST from '@/untils/config.js'
import store from '@/store/index'

/**
 * @description 乘客列表、保存
 */
const PASSENGER_ACTIONS = {
  async getPassengerList ({ commit, state }, data) {
    const REQ_PARAMS = {
      url: API_LIST.passenger_list,
      params: data,
      header:{
        'Authorization': store.getters['LOGIN/accountToken']
      }
    }
    return new Promise((resolve, reject) => {
      $ajax.get(REQ_PARAMS).then(res =>{
        if(res.status == 200) {
          commit('setPassengerList', res.data)
        }
        resolve(res)
      })
    })
  },
  async passengerSave ({ commit, state }, data) {
    const REQ_PARAMS = {
      url: API_LIST.passenger_save,
      params: data,
      header:{
        'Authorization': store.getters['LOGIN/accountToken'],
      }
    }
    return new Promise((resolve, reject) => {
      $ajax.post(REQ_PARAMS).then(res =>{
        console.log(res)
        resolve(res)
      })
    })
  }
}

export default PASSENGER_ACTIONS



