
import $ajax from '@/untils/ajax-service.js'
import API_LIST from '@/untils/config.js'
import store from '@/store/index'

/**
 * @description 获取城市列表
 */
const CITY_ACTIONS = {
  async getCityList ({ commit, state }, data) {
    const REQ_PARAMS = {
      url: API_LIST.city_list,
      params: data,
      header:{
        'Authorization': store.getters['LOGIN/accountToken']
      }
    }
    return new Promise((resolve, reject) => {
      $ajax.get(REQ_PARAMS).then(res =>{
        if(res.status == 200) {
          commit('setCityList', res.data)
        }
        resolve(res)
      })
    })
  }
}

export default CITY_ACTIONS



