// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import Vue from 'vue'
import FastClick from 'fastclick'
import VueRouter from 'vue-router'
import App from './App'
import store from './store'
import router from './router'
import { corJs, corNative, corPlugin } from 'corlib'

Vue.use(VueRouter)
require('@/untils/vux-plugin.js')
require('@/assets/css/vux.css')
// require('@/assets/font/iconfont.css')

require('corlib/lib/iconfont.css')
require('corlib/lib/flex.css')
require('corlib/lib/base.css')
require('corlib/lib/rem.js')


FastClick.attach(document.body)

Vue.config.productionTip = false

/* 从localstorage刷新登录信息导store */
store.dispatch('LOGIN/reLoginLocal')

/* eslint-disable no-new */
new Vue({
  router,
  store,
  render: h => h(App)
}).$mount('#app-box')



